
import { isNumber } from "@/common";
import {
  STAR_BACKGROUND_IMAGE_CLASS,
  TWeaponEntry,
  WEAPON_MASTER_LIST,
  TWeaponTypeKey,
  WEAPON_IMG_SRC,
} from "@/master";
import { computed, defineComponent, reactive, ref } from "vue";
import CompositionFunction from "./CompositionFunction.vue";

export default defineComponent({
  name: "WeaponOwnList",
  setup() {
    const { displayName, targetValue } = CompositionFunction();

    const bgImageClass = (item: TWeaponEntry) =>
      (" " + STAR_BACKGROUND_IMAGE_CLASS[item.レアリティ]) as string;

    const weaponTypeList = computed(
      () => Object.keys(WEAPON_IMG_SRC) as TWeaponTypeKey[]
    );
    const weaponSrc = (weapon: TWeaponTypeKey) => WEAPON_IMG_SRC[weapon] as string;
    const weaponList = (weaponType: TWeaponTypeKey) =>
      WEAPON_MASTER_LIST[weaponType] as any[];
    const selectedWeaponType = ref(weaponTypeList.value[0] as any);

    const refineObj = reactive({} as { [key: string]: number | null });
    for (const weaponType of weaponTypeList.value) {
      for (const item of weaponList(weaponType)) {
        refineObj[item.key] = null;
      }
    }
    if (localStorage["武器所持状況"]) {
      const savedObj = JSON.parse(localStorage["武器所持状況"]);
      Object.keys(savedObj).forEach((key) => {
        if (isNumber(savedObj[key])) {
          refineObj[key] = Number(savedObj[key]);
        }
      });
    }
    const changed = ref(false);
    const savable = ref(false);

    const notOwnedClass = (item: TWeaponEntry) =>
      refineObj[item.key] == null ? " not-owned" : "";

    const onClick = (item: TWeaponEntry) => {
      if (refineObj[item.key] === null) {
        refineObj[item.key] = 1;
      } else if (refineObj[item.key] == 5) {
        refineObj[item.key] = null;
      } else {
        let current = refineObj[item.key] as number;
        refineObj[item.key] = current + 1;
      }
      changed.value = true;
    };

    const save = () => {
      const saveObj = {} as { [key: string]: number | null };
      Object.keys(refineObj)
        .filter((s) => refineObj[s] !== null)
        .forEach((key) => {
          saveObj[key] = refineObj[key];
        });
      localStorage.setItem("武器所持状況", JSON.stringify(saveObj));
      savable.value = false;
      changed.value = false;
    };

    return {
      displayName,
      targetValue,

      bgImageClass,
      weaponTypeList,
      weaponSrc,
      weaponList,
      selectedWeaponType,
      refineObj,
      onClick,
      changed,
      savable,
      save,
      notOwnedClass,
    };
  },
});
